@import "components/variables";
@import "components/mixin";
@import "components/header";
@import "components/howToMakeOrder";
@import "components/addres";
@import "components/deliveryPopup";
@import "components/fixed_nav";
@import "components/authorization";
@import "components/aside_buttons";

body {
  .header{
    position: unset!important;
  }
  .content {
    padding-top: 25px;
    width: 100%;
    .containerParent();
    .container {
      .containerChild();
      margin-bottom: 100px;
      display: grid;
      grid-template-areas: "breadcrumb breadcrumb"
                           "busket order";
      grid-template-columns: 1fr 35%;
      .content__catalog__breadcrumb {
        grid-area: breadcrumb;
        ul {
          display: flex;
          li {
            a {
              font: .875em/1em @sansregular;
              color: #474747;
            }
            span {
              font: .875em/1em @sansregular;
              color: #474747;
            }
          }
        }
      }
      .content__busket{
        grid-area: busket;
        margin-right: 25px;
        h5 {
          font: 2.25em/2.375em @serifitalic;
          color: @linkColor;
        }
        ul{
          padding: 20px;
          border: 2px solid #e6e6e6;
          border-radius: 15px;
          li{
            margin: 40px 0;
            div{
              display: flex;
              justify-content: space-between;
              align-items: center;
              .value{
                width: 45%;
                font: 1em/1.125em @sansregular;
                color: @linkColor;
              }
              .quantitu{
                span{
                  display: inline-block;
                  width: 25px;
                  height: 40px;
                  font: 1.375em/40px @sansregular;
                  text-align: center;
                  color: @linkColor;
                  background-color: #77c9cc;
                  user-select: none;
                  &:hover{
                    background-color: @mainColor;
                    cursor: pointer;
                    transition: .5s;
                  }
                }
                .minus{
                  border-top-left-radius: 5px;
                  border-bottom-left-radius: 5px;
                }
                .plus{
                  border-top-right-radius: 5px;
                  border-bottom-right-radius: 5px;
                }
                input{
                  width: 35px;
                  height: 34px;
                  font-size: 1em;
                  font-family: @sansregular;
                  text-align: center;
                  color: @linkColor;
                  border: 2px solid #77c9cc;
                  outline: none;
                }
              }
              .price{
                font: 1.375em/1.5em @sansbold;
                color: @linkColor;
                span{
                  font: .75em/.875em @sansregular;
                }
              }
              button{
                width: 125px;
                height: 40px;
                font: .875em/40px @sansregular;
                color: #fff;
                text-align: center;
                text-transform: uppercase;
                background-color: @mainColor;
                border: none;
                border-radius: 5px;
                outline: none;
                &:hover{
                  cursor: pointer;
                }
              }
              .delete{
                display: inline-block;
                font-size: 1.5em;
                color: @mainColor;
                &:hover{
                  cursor: pointer;
                }
              }
            }
          }
        }
        .total{
          margin-top: 25px;
          display: flex;
          h6{
            font: 1.125em/1.25em @sansbold;
            color: @linkColor;
          }
          p{
            margin-left: 50px;
            font: 1.125em/1.25em @sansbold;
            color: @linkColor;
            span{
              font: .75em/1.125em @sansregular;
            }
          }
        }
        .costumerInfo{
          margin-top: 60px;
          display: flex;
          flex-wrap: wrap;
          flex-direction: column;
          form {
            p {
              display: inline-flex;
              margin: 0 5px;
              position: relative;
              input {
                padding-left: 10px;
                width: 250px;
                height: 40px;
                font: 1em/1.125em @serifregular;
                color: #828282;
                outline: none;
                border: 2px solid #e6e6e6;
                border-radius: 5px;
              }
              .asterisk {
                position: absolute;
                top: 42.5%;
                right: 20px;
                font-size: .35em;
                color: #ff0000;
              }
            }
            .error{
              border: 1px solid red;
            }
            #name-error,
            #tel-error{
              padding: 10px;
              position: absolute;
              left: -5px;
              top: 60px;
              text-align: center;
              background: #ffffa1;
              border: 1px solid #464040!important;
              border-radius: 5px;
              &:before{
                content: '';
                position: absolute;
                top: -44px;
                left: 23px;
                border: 22px solid transparent;
                border-bottom: 22px solid #464040;
              }
              &:after{
                content: '';
                position: absolute;
                top: -40px;
                left: 25px;
                border: 20px solid transparent;
                border-bottom: 20px solid #ffffa1;
              }
            }
          }
          textarea{
            margin: 10px 0 30px 5px;
            padding: 10px;
            width: 514px;
            height: 90px;
            font: 1em/1.125em @serifregular;
            color: #828282;
            resize: none;
            border: 2px solid #e6e6e6;
            border-radius: 5px;
            outline: none;
          }
          button{
            width: 250px;
            height: 40px;
            font: .875em/40px @sansregular;
            color: #fff;
            text-align: center;
            text-transform: uppercase;
            background-color: @mainColor;
            border: none;
            border-radius: 5px;
            outline: none;
            &:hover{
              cursor: pointer;
            }
          }
        }
      }
      .content__howToMakeOrder {
        grid-area: order;
        margin: 95px 0;
        flex-direction: column;
        div{
          margin: 0 0 50px 0;
          width: 100%;
        }
      }
    }
  }
}